// 验证密码
export function validPassWord (val) {
  const re = /^[a-zA-Z]\w{5,17}$/
  return re.test(val)
}
// 验证手机号码
export function validPhoneNo (val) {
  const re = /^1[34578]\d{9}$/
  return re.test(val)
}
// 验证邮箱
export function validEmail (val) {
  const re = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/
  return re.test(val)
}



