<template>
  <div class="op-authority-permission">
    <condition-frame class="op-query">
      <!--表单布局 主要是为了条件区域进行查询-->
        <el-form :model="formData" :inline="true" ref="formData" label-width="0">
          <el-form-item   prop="user">
                <el-input type="user" v-model.number="formData.user"  placeholder="姓名/手机号" auto-complete="off" class="sidebar-search"></el-input>
          </el-form-item>
           <el-form-item prop="startDate">
            <el-date-picker
              v-model="formData.startDate"
              type="date"
              placeholder="开始日期"
              value-format="yyyy-MM-dd"
              class="sidebar-search">
            </el-date-picker>
          </el-form-item>
          <el-form-item prop="endDate">
            <el-date-picker
              v-model="formData.endDate"
              type="date"
              placeholder="结束日期"
              value-format="yyyy-MM-dd"
              class="sidebar-search">
            </el-date-picker>
          </el-form-item>
       
          <el-form-item>
            <!-- <router-link to="/addplace"> -->
                <el-button type="primary" @click="fetchData" size="small">查询</el-button>
                <el-button @click="resetForm('formData')"  size="small">重置</el-button>
                <el-button type="primary" size="small" @click = "increVisible = true">添加</el-button>
                <el-button type="primary" size="small" @click = "exportVisble = !exportVisble">导出</el-button>
            <!-- </router-link> -->
          </el-form-item>
        </el-form>
    </condition-frame>
    <info-frame>
      当前总条数
      <op-badge>{{totalCount}}</op-badge>
      条
    </info-frame>
    <grid-frame>
      <template>
        <el-table
          :data="datas"
           border
          :height = "theight"
          ref="table"
          stripe
          v-loading.body="listLoading" element-loading-text="拼命加载中"  highlight-current-row fit cell-class-name = "h40"
          >
          <el-table-column
            prop="userCode"
            label="用户编码"
            width="180"
            class-name="text">
          </el-table-column>
          <el-table-column
            prop="userName"
            label="名称"
            width="280">
          </el-table-column>
          <el-table-column
            prop="mobileNum"
            label="手机号码"
            class-name="num">
          </el-table-column>
          <el-table-column
            label="性别" prop="sex">
            <template slot-scope="scope">
              {{scope.row.sex | sexFilter}}
            </template>
          </el-table-column>
          <el-table-column
            prop="regionName"
            label="区划">
          </el-table-column>
          <el-table-column fixed="right" label="操作" width="240">
                  <template slot-scope="scope">
                      <el-button type="text" size="small" @click = "isEditStaff('ruleForm', scope.row)">编辑</el-button>
                      <el-button type="text" size="small" @click = "editorPwDia(scope.row)">修改密码</el-button>
                      <el-button type="text" size="small" @click = "roleDiaShow(scope.row)">设置角色</el-button>
                      <el-button type="text" size="small" @click = "removeStaff(scope.row)">删除</el-button>
                  </template>
              </el-table-column>
        </el-table>
      </template>
      <pagination></pagination>
    </grid-frame>
      <exportExcel :exportHeader="exportHeader" :exportFilter="exportFilter" :dialogVisble.sync="exportVisble" @getExportSize="getExportSize" :getexportList = "getexportList"></exportExcel>
      <!-- 新增运营管理人员 -->
      <el-dialog :title="title" :visible.sync="increVisible"  width="42%" class="add" @close = "isAddStaff('ruleForm')">
          <el-form :inline="true" ref="ruleForm" :model="ruleForm" :rules="addrules" label-width="160px"  class="demo-form-inline">
              <el-form-item label="头像">
                  <!-- <el-button type="primary">上传<i class="el-icon-upload el-icon--left"></i></el-button> -->
                  <el-upload
                    class="avatar-uploader"
                    action="string"
                    :http-request = "uploadAvatar"
                    :show-file-list="false"
                    :on-success="handleAvatarSuccess"
                    :before-upload="beforeAvatarUpload"
                    >
                    <img v-if="imageUrl" :src="imageUrl" class="avatar">
                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                  </el-upload>
              </el-form-item>
              <br>
              <el-form-item label="选择省市区" prop="selectedRegions">
                  <el-cascader
                     v-model="ruleForm.selectedRegions"
                    :options="cityList"
                    change-on-select
                    @change="regionChange"
                    :props="props"
                  ></el-cascader> 
              </el-form-item>
              <el-form-item label="用户类型" prop="userType">
                  <el-select v-model="ruleForm.userType" placeholder="请选择">
                      <el-option :label="val" :value="key" v-for = "(val,key) in userTypeList" :key = "key"></el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label="父级用户" v-show = "ruleForm.userType == 5">
                 <el-select
                    v-model="ruleForm.parentId"
                    filterable
                    remote
                    reserve-keyword
                    placeholder="请输入关键词"
                    :remote-method="getSettleMentList"
                    :loading="loading">
                    <el-option
                      v-for="item in ruleForm.settleMentList"
                      :key="item.id"
                      :label="item.name"
                      :value="item.id">
                    </el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label="真实姓名" prop="trueName">
                  <el-input v-model="ruleForm.trueName" placeholder="请输入"></el-input>
              </el-form-item>
              <el-form-item label="性别" prop="sex">
                  <el-select v-model="ruleForm.sex" placeholder="请选择">
                      <el-option label="请选择" value="0"></el-option>                    
                      <el-option label="男" value="1"></el-option>
                      <el-option label="女" value="2"></el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label="手机号码" prop="mobile">
                  <el-input v-model="ruleForm.mobile" placeholder="请输入"></el-input>
              </el-form-item>
           
              <el-form-item label="邮箱" prop="email">
                  <el-input v-model="ruleForm.email" placeholder="请输入"></el-input>
              </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
              <el-button @click="increVisible=false">取 消</el-button>
              <el-button type="primary" @click="addStaff('ruleForm')">确 定</el-button>
            </span>
      </el-dialog>
      <!-- 修改密码 -->
       <el-dialog title="修改密码" :visible.sync="pwVisible" width="30%">
          <el-form :inline="true" ref="pwForm" :model="pwForm" :rules="pwRules" label-width="100px" class="demo-ruleForm">
              <el-form-item label="新密码" prop="newPassWord">
                  <el-input v-model="pwForm.newPassWord" type="password"  placeholder="请输入"></el-input>                 
              </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
              <el-button @click="pwVisible=false">取 消</el-button>
              <el-button type="primary" @click="editorPw('pwForm')">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 设置角色 -->
          <el-dialog title="设置角色" :visible.sync="roleVisible" width="44%" class="role" @close="closeRoleDia">
          <el-form :inline="true" ref="roleForm" :model="roleForm" :rules="roleRules" label-width="80px" class="demo-ruleForm">
              <el-form-item label="用户名" prop="name">
                  <el-input v-model="roleForm.name"  type="size"  placeholder="请输入" disabled></el-input>                 
              </el-form-item>
               <el-form-item label="角色" prop="role"> 
                   <el-select
                    v-model="roleForm.role"
                    filterable
                    remote
                    reserve-keyword
                    placeholder="请输入关键词"
                    :remote-method="getRole"
                    :loading="loading">
                    <el-option
                      v-for="item in roleForm.roleList"
                      :key="item.id"
                      :label="item.roleName"
                      :value="item.id">
                    </el-option>
                  </el-select>
              </el-form-item>
                  <el-button type="primary" @click="addUserRole('roleForm')">添加</el-button>
          </el-form>
          <!-- <span slot="footer" > -->
             <el-table
                :data="userRoleList"
                border
                fit
                stripe
                cell-class-name = "h40"
                style="width: 100%"
                class="diaTable">
                <el-table-column
                  prop="roleCode"
                  label="角色编号"
                >
                </el-table-column>
                <el-table-column
                  prop="roleName"
                  label="角色名称"
                >
                </el-table-column>
                <el-table-column label="操作">
                  <template slot-scope="scope">
                    <el-button
                      size="mini"
                      type="danger"
                      @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                  </template>
                </el-table-column>
              </el-table>
          <!-- </span> -->  
      </el-dialog>
    </div>
</template>
<script>
  import { mapGetters } from 'vuex'
  import { getStaffList, addUser, removeUser, editUser, editPassword, getUserRoles, setUserRole, removeUserRole } from '@/api/platform/staffmanage'
  import { getRoleList } from '@/api/platform/rolemanage'
  import { uploadFile, getOssByCondition, getSettlementInfoList } from '@/api/base'
  import { validPassWord, validPhoneNo, validEmail } from '@/utils/validate'
  import { sexFilter } from '@/utils/filters'
  import opBadge from 'components/base/opBadge.vue'
  import conditionFrame from 'components/base/conditionFrame.vue'
  import infoFrame from 'components/base/infoFrame.vue'
  import gridFrame from 'components/base/gridFrame.vue'
  import exportExcel from 'components/base/exportexcel/index'
  import pagination from 'components/base/pagination/index.vue'
  export default {
    name: 'op-authority-permission',
    data () {
      var svalidatePass = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('请输入新密码'))
        } else if (!validPassWord(value)) {
          callback(new Error('以字母开头，长度在6~18之间，只能包含字母、数字和下划线'))
        } else {
          callback()
        }
      }
      var svalidPhoneNo = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('请输入手机号码'))
        }
        if (!validPhoneNo(value)) {
          callback(new Error('请输入正确的手机号码'))
        } else {
          callback()
        }
      }
      var svalidEmail = (rule, value, callback) => {
        if (!validEmail(value) && value) {
          callback(new Error('请输入正确邮箱格式'))
        } else {
          callback()
        }
      }
      var svalidRegion = (rule, value, callback) => {
        if (!value || value.length === 0) {
          callback(new Error('请选择省市区'))
        } else {
          callback()
        }
      }
      var validRole = (rule, value, callback) => {
        if (!value || value.length === 0) {
          callback(new Error('请输入角色名称'))
        } else {
          callback()
        }
      }
      return {
        exportList: [],
        increVisible: false,
        pwVisible: false,
        exportVisble: false,
        roleVisible: false,
        listLoading: true,
        isEdit: false,
        totalCount: 0,
        formData: {
          user: '',
          startDate: '',
          endDate: ''
        },
        ruleForm: {
          userType: '',
          parentId: '',
          trueName: '',
          sex: '',
          mobile: '',
          selectedRegions: [],
          settleMentList: [],
          checkedRegion: '',
          filename: '',
          userId: ''
        },
        title: '添加运营管理人员',
        roleForm: {
          userId: '',
          name: '',
          role: '',
          roleList: []
        },
        roleRules: {
          role: [
            {required: true, validator: validRole, trigger: 'change'}
          ]
        },
        pwForm: {
          newPassWord: '',
          userId: ''
        },
        pwRules: {
          newPassWord: [
            {validator: svalidatePass, trigger: 'blur'}
          ]
        },
        addrules: {
          userType: [
            {required: true, message: '请选择用户类型', trigger: 'change'}
          ],
          trueName: [
            {required: true, message: '请输入真实姓名', trigger: 'blur'}
          ],
          sex: [
            {required: true, message: '请选择性别', trigger: 'change'}
          ],
          mobile: [
            {required: true, validator: svalidPhoneNo, trigger: 'blur'}
          ],
          email: [
            {validator: svalidEmail, trigger: 'blur'}
          ],
          selectedRegions: [
            {required: true, validator: svalidRegion, trigger: 'change'}
          ]
        },
        datas: [],
        imageUrl: '',
        userTypeList: {0: '普通注册用户', 1: '城市管理平台巡检用户', 2: '城市管理平台用户', 3: '慧停车管理平台用户', 4: '停车场管理平台用户', 5: '结算方', 6: '稽查端', 7: '关联方', 8: '运营方', 9: '岗亭收费人', 10: '安装人员'},
        props: {
          label: 'name',
          value: 'id',
          children: 'list'
        },
        loading: false,
        userRoleList: [],
        exportHeader: [],
        exportFilter: []
      }
    },
    created () {
      this.$store.dispatch('Get_CITYLIST')
      this.fetchData()
    },
    computed: {
      ...mapGetters(['pageNum', 'pageSize', 'cityList']),
      theight () {
        return document.body.offsetHeight - 300
      }
    },
    watch: {
      pageSize () {
        this.fetchData()
      },
      pageNum () {
        this.fetchData()
      }
    },
    components: {conditionFrame, infoFrame, gridFrame, opBadge, pagination, exportExcel},
    methods: {
      /** ================================= 获取人员列表================================= */
      async fetchData (size) {
        this.listLoading = true
        this.listQuery = {
          keyword: this.formData.user,
          pageNo: this.pageNum,
          pageSize: this.pageSize,
          startDate: this.formData.startDate,
          endDate: this.formData.endDate
        }
        let res = await getStaffList(this.listQuery)
        if (res.returnObject) {
          this.datas = res.returnObject.list
          this.totalCount = res.returnObject.total
          this.$store.dispatch('SET_TOTALCOUNT', this.totalCount)
        } else {
          this.datas = []
          this.totalCount = 0
          this.$store.dispatch('SET_TOTALCOUNT', 0)
        }
        this.listLoading = false
      },
      /** ================================= 添加人员================================= */
      // 编辑人员显示弹窗
      isEditStaff (formName, row) {
        const regionId = row.regionId
        const p = regionId.substring(0, 2) + '0000'
        const c = regionId.substring(0, 4) + '00'
        this.title = '编辑运营管理人员'
        this.isEdit = true
        this.ruleForm.selectedRegions = [p, c, regionId]
        this.ruleForm.trueName = row.userName
        this.ruleForm.mobile = row.mobileNum
        this.ruleForm.userType = row.userType.toString()
        this.ruleForm.checkedRegion = regionId
        this.ruleForm.sex = row.sex
        this.ruleForm.email = row.email
        this.ruleForm.userId = row.userId
        this.ruleForm.filename = row.avatar
        this.increVisible = true
        if (row.avatar !== '') {
          this.getAvatar(row.avatar)
        } else {
          this.imageUrl = ''
        }
        if (row.parentId !== '0' && row.parentId !== '') {
          this.getSettleMentList(row.parentSettleName)
          this.ruleForm.parentId = row.parentId
        }
      },
      // 弹窗关闭回调
      isAddStaff (formName) {
        if (this.isEdit) {
          this.title = '添加运营管理人员'
          this.imageUrl = ''
          this.isEdit = false
          this.ruleForm = {
            userType: '',
            parentId: '',
            trueName: '',
            sex: '',
            mobile: '',
            selectedRegions: [],
            settleMentList: [],
            checkedRegion: '',
            filename: '',
            userId: ''
          }
          this.$refs[formName].resetFields('selectedRegions')
        }
      },
      // 头像上传
      handleAvatarSuccess (res, file) {
        this.imageUrl = URL.createObjectURL(file.raw)
      },
      // 头像上传之前的回调
      beforeAvatarUpload (file) {
        // const isJPG = file.type === 'image/jpeg'
        const isLt1M = file.size / 1024 / 1024 < 1
        // if (!isJPG) {
        //   this.$message.error('上传头像图片只能是 JPG 格式!')
        // }
        if (!isLt1M) {
          this.$message.error('上传头像图片大小不能超过 1MB!')
        }
        return isLt1M
      },
      // 自定义上传
      async uploadAvatar (item) {
        let formData = new FormData()
        formData.append('file', item.file)
        formData.append('type', 'image')
        let res = await uploadFile(formData, 'avatar')
        this.ruleForm.filename = res.returnObject.filename
      },
      // 城区选择
      regionChange (val) {
        this.ruleForm.checkedRegion = val.slice(-1)[0]
      },
      // 获取结算房列表
      async getSettleMentList (query) {
        if (query !== '') {
          this.loading = true
          this.listQuery = {
            key: query,
            pageNo: 1,
            pageSize: 20
          }
          let res = await getSettlementInfoList(this.listQuery)
          this.ruleForm.settleMentList = res.returnObject.list
          this.loading = false
        } else {
          this.ruleForm.settleMentList = []
        }
      },
      // 添加或编辑人员
      async addStaff (formName) {
        let valid = await this.$refs[formName].validate()
        if (valid) {
          this.listQuery = {
            avatar: this.ruleForm.filename,
            registerSource: 6,
            mac: '',
            regionId: this.ruleForm.checkedRegion,
            regionNo: this.ruleForm.checkedRegion,
            userType: this.ruleForm.userType,
            parentId: this.ruleForm.parentId,
            userName: this.ruleForm.trueName,
            sex: this.ruleForm.sex,
            mobile: this.ruleForm.mobile,
            email: this.ruleForm.email
          }
          let res
          if (this.isEdit) {
            this.$set(this.listQuery, 'userId', this.ruleForm.userId)
            res = await editUser(this.listQuery)
          } else {
            res = await addUser(this.listQuery)
          }
          if (res) {
            this.increVisible = false
            this.$message({
              type: 'success',
              message: res.message
            })
          }
          this.$refs[formName].resetFields()
          this.fetchData()
        } else {
          return false
        }
      },
      /** ================================= 编辑人员================================= */
      // 获取人员头像
      async getAvatar (key) {
        let res = await getOssByCondition(key)
        this.imageUrl = res.returnObject.url
      },
      /** ================================= 修改密码================================= */
      editorPwDia (row) {
        this.pwVisible = true
        this.pwForm.userId = row.userId
      },
      async editorPw (formName) {
        let valid = await this.$refs[formName].validate()
        if (valid) {
          let res = await editPassword(this.pwForm.userId, this.pwForm.newPassWord)
          if (res) {
            this.pwVisible = false
            this.$message({
              type: 'success',
              message: '修改密码成功!'
            })
            this.$refs[formName].resetFields()
          }
        }
      },
      /** ================================= 设置角色================================= */
      // 获得角色列表
      async getRole (query) {
        if (query !== '') {
          this.loading = true
          this.listQuery = {
            roleCode: '',
            roleName: query,
            pageNo: 1,
            pageSize: 100
          }
          let res = await getRoleList(this.listQuery)
          this.roleForm.roleList = res.returnObject.list
          this.loading = false
        } else {
          this.roleForm.roleList = []
        }
      },
      // 显示角色弹窗
      roleDiaShow (row) {
        this.roleVisible = true
        this.roleForm.name = row.userName
        this.roleForm.userId = row.userId
        this.getUserRoleList(this.roleForm.userId)
      },
      // 获取用户角色列表
      async getUserRoleList (userId) {
        let res = await getUserRoles(userId)
        this.userRoleList = res.returnObject
      },
      // 关闭设置角色弹窗
      closeRoleDia () {
        this.roleForm.name = ''
        this.userId = ''
        this.userRoleList = []
      },
      // 添加用户角色
      async addUserRole () {
        await this.$refs['roleForm'].validate()
        this.listQuery = {
          userId: this.roleForm.userId,
          roleId: this.roleForm.role
        }
        let res = await setUserRole(this.listQuery)
        if (res) {
          this.$message({
            type: 'success',
            message: '设置成功!'
          })
          this.roleForm.role = ''

          this.getUserRoleList(this.roleForm.userId)
        } else {
          this.$message({
            type: 'success',
            message: '设置失败!'
          })
        }
      },
      // 删除用户角色
      async handleDelete (index, row) {
        let query = {
          userId: this.roleForm.userId,
          roleId: row.roleId
        }
        let res = await removeUserRole(query)
        if (res) {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.getUserRoleList(this.roleForm.userId)
        } else {
          this.$message({
            type: 'success',
            message: '删除失败!'
          })
        }
      },
      /** ================================= 删除人员================================= */
      async removeStaff (row) {
        let flag = await this.$confirm('是否永久删除该用户?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        if (flag) {
          let res = await removeUser(row.userId)
          if (res) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            this.fetchData()
          }
        } else {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        }
      },
      // 重置查询表单
      resetForm (formName) {
        this.$store.dispatch('SET_PAGESIZE', 15)
        this.$store.dispatch('SET_PAGENUM', 1)
        this.$refs[formName].resetFields()
        this.fetchData()
      },
      // 导出
      getExportSize () {
        const tableData = this.$refs.table.$slots.default
        tableData.forEach((ele) => {
          if (ele.componentOptions && ele.componentOptions.propsData.prop) {
            let propData = ele.componentOptions.propsData
            this.exportHeader.push(propData.label)
            this.exportFilter.push(propData.prop)
          }
        })
      },
      getexportList (size) {
        let listQuery = {
          keyword: this.formData.user,
          pageNo: this.pageNum,
          pageSize: size,
          startDate: this.formData.startDate,
          endDate: this.formData.endDate
        }
        return new Promise((resolve, reject) => {
          getStaffList(listQuery).then((res) => {
            let list = res.returnObject.list
            list.map(v => {
              v.sex = sexFilter(v.sex)
            })
            resolve(list)
          }, err => reject(err))
        })
      }
    }
  }
</script>

<style lang="less">
  .op-authority-permission {
    width: 100%;
    height: 100%;
    .add .el-dialog{
      min-width: 800px;
    }
    .role .el-dialog{
      min-width: 740px;
    }
     .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  }
</style>



