import fetch from '@/utils/fetch'
/**
 * 获取运营人员列表
 * @export
 * @param {any} {keyword, pageNo, pageSize, startDate, endDate}
 */
export function getStaffList ({keyword, pageNo, pageSize, startDate, endDate}) {
  return fetch({
    url: '/authorityUser/getUserByPage',
    method: 'get',
    params: {
      keyword,
      pageNo,
      pageSize,
      startDate,
      endDate
    }
  })
}
/**
 * 添加用户
 * @export
 * @param {any} avatar, regionId, parentId, email, mac, mobile, registerSource
 */
export function addUser ({avatar, regionNo, regionId, parentId, email, mac, mobile, registerSource, userName, userType, sex}) {
  return fetch({
    url: '/authorityUser/addUser',
    method: 'post',
    data: {
      avatar,
      regionNo,
      regionId,
      parentId,
      email,
      mac,
      mobile,
      registerSource,
      userName,
      userType,
      sex
    }
  })
}

/**
 * 编辑用户
 * @export
 * @param {any} avatar, regionId, parentId, email, mac, mobile, registerSource
 */
export function editUser ({avatar, regionId, parentId, email, mac, mobile, registerSource, userName, userType, sex, userId}) {
  return fetch({
    url: '/authorityUser/updateUser',
    method: 'post',
    data: {
      avatar,
      regionId,
      parentId,
      email,
      mac,
      mobile,
      registerSource,
      userName,
      userType,
      sex,
      userId
    }
  })
}

/**
 * 删除用户
 * @export
 * @param {any} id
 */
export function removeUser (userId) {
  return fetch({
    url: '/authorityUser/removeUser',
    method: 'post',
    data: {
      userId
    }
  })
}
// 修改密码
export function editPassword (userId, password) {
  return fetch({
    url: '/authorityUser/editPassword',
    method: 'post',
    data: {
      userId,
      password
    }
  })
}
// 获取用户角色
export function getUserRoles (userId) {
  return fetch({
    url: '/authorityUser/getUserRoles',
    method: 'get',
    params: {userId}
  })
}
// 设置用户角色
export function setUserRole ({userId, roleId}) {
  return fetch({
    url: '/authorityUser/setUserRole',
    method: 'post',
    data: {
      userId,
      roleId
    }
  })
}
// 删除用户角色
export function removeUserRole ({userId, roleId}) {
  return fetch({
    url: '/authorityUser/removeUserRole',
    method: 'post',
    data: {
      userId,
      roleId
    }
  })
}



